<template>
  <van-cell-group>
    <van-field v-model="name" label="" placeholder="" required/>
    <van-radio-group v-model="gender">
      <van-cell-group>
        <van-cell class="required">
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>
    <van-field v-model="stuId" label="" placeholder="" required/>
    <van-field v-model="major" label="" placeholder="" />
    <van-field v-model="className" label="" placeholder="" />
    <van-field v-model="phoneNumber" placeholder="" label="" />
    <van-field v-model="wx" placeholder="" label="" />
    <!-- <van-field v-model="birthday" label="" placeholder="1997-06-13" />
    <van-radio-group v-model="minority">
      <van-cell-group>
        <van-cell>
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>
    <van-field v-model="volk" label="" placeholder="" />
    <van-field v-model="nativePlace" label="" placeholder="" />
    <van-field v-model="dormId" label="" placeholder="" />
    <van-field v-model="enrolment" label="" placeholder="" />
    <van-radio-group v-model="NDstudent">
      <van-cell-group>
        <van-cell>
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group>
    <van-radio-group v-model="partyMember">
      <van-cell-group>
        <van-cell>
          <div class="van-cell__title label">
            <span class="van-cell__text"></span>
          </div>
          <van-radio name="" class="radio"></van-radio>
          <van-radio name="" class="radio"></van-radio>
        </van-cell>
      </van-cell-group>
    </van-radio-group> -->
  </van-cell-group>
</template>

<script>
export default {
  data() {
    return {
      name: '',
      gender: '',
      stuId: '',
      major: '',
      className: '',
      phoneNumber: '',
      wx: ''
      // birthday: '',
      // minority: '',
      // volk: '',
      // nativePlace: '',
      // dormId: '',
      // enrolment: '',
      // NDstudent: '',
      // partyMember: ''
    }
  },
  mounted() {
      const form = JSON.parse(localStorage.getItem('basic'));
      if (form) {
        this.name = form.name;
        this.gender = form.gender;
        this.stuId = form.stuId;
        this.major = form.major;
        this.className = form.className;
        this.phoneNumber = form.phoneNumber;
        this.wx = form.wx;
        // this.birthday = form.birthday;
        // this.minority = form.minority;
        // this.volk = form.volk;
        // this.nativePlace = form.nativePlace;
        // this.dormId = form.dormId;
        // this.enrolment = form.enrolment;
        // this.NDstudent = form.NDstudent;
        // this.partyMember = form.partyMember;
      }
  }
}
</script>

<style scoped>
.van-cell__value--alone {
  display: flex;
}
.label {
  align-self: center;
}
.van-cell__title {
  min-width: 90px;
  display: inline-block;
  text-align: center;
  line-height: 32px;
}
.radio {
  display: inline-block;
  padding: 0 5px;
}
.required::before {
  content: '*';
  position: absolute;
  left: 7px;
  padding-left: inherit;
  font-size: 14px;
  color: #f44;
}
.van-cell--required::before {
  padding-left: inherit;
}
</style>



